<template>
	<view>
		<!-- 内容区 -->
		<view class="content">
			<!-- 左上日期 -->
			<view id="date">
				<view id="date_d" class="date_">{{date_d}}</view>
				<view id="line" class="date_"></view>
				<view id="date_y_m">
					<view id="date_m">{{date_m}}</view>
				</view>
			</view>
			<!-- 右上功能键 -->
			<view class="right-top">
				<view id="top_tool_1">
					<view class="t_tool iconfont icon-ico-back"></view>
				</view>
				<view id="top_tool_2">
					<view class="t_tool iconfont icon-ico-forward"></view>
				</view>
				<view id="top_tool_3">
					<view class="t_tool cuIcon-close fs" @click="clear()"></view>
				</view>
			</view>
			<!-- 画布 -->
			<canvas id="myCanvas" canvas-id="myCanvas" disable-scroll="true" @touchmove="tmove" @touchstart="tstart"
				@touchend="tend" @touchcancel="tcancel"></canvas>

			<!-- 悬浮工具 -->
			<view>
				

				<view id="tool_box">
					<view class="cuIcon-down a_tool_1" id="a_tool_1"></view>
					<view class="iconfont icon-baocuntupian a_tool_2" id="a_tool_1"></view>
					<view class="iconfont icon-xiangpi a_tool_3 " id="a_tool_1"></view>
					<view class="cuIcon-write a_tool_4" id="a_tool_1" @click="mPen()"></view>
					<view id="myPen" v-show="myPen_flag">
						<view id="myPen_color">
							<view class="myColor" v-for="i in colorArr" :key="i.color" :style="{background: i.color}"
								@click="updateColor(i.color)"></view>
						</view>
						<view id="myPen_thickness">
							<view class="myThick" v-for="j in thickness" :key="j.thickness"
								:style="{width: j.thickness/5+'rpx'}" @click="updateThick(j.thickness)"></view>
						</view>
						<view id="myPen_weight"></view>
					</view>
				</view>
			</view>
		</view>
		<!-- 自定义tabbar -->
		<view class="" v-if="show" @tap="switchDialog"></view>
		<Tabbar nowIndex="0" @switchDialog="switchDialog"></Tabbar>
	</view>
</template>

<script>
	//import Mycanvas from "../../static/js/theCanvas.js";

	var x = 0;
	var y = 0;
	import Tabbar from "../../components/tabbar/tabbar";
	export default {
		components: {
			Tabbar
		},
		data() {
			return {
				// 自定义tabbar页面展示
				show: false,
				flag:false,
				myPen_flag: false,
				'date_d': '00',
				'date_y': '2020',
				'date_m': 'Aay',
				ctx: '',
				point: [],
				colorArr: [ //画笔颜色
					{
						color: "#fe4365",
						active: true
					},
					{
						color: "#f9cdad",
						active: false
					},
					{
						color: "#19CAAD",
						active: false
					},
					{
						color: "#a7dce0",
						active: false
					},
					{
						color: "#b3a8e9",
						active: false
					},
					{
						color: "#03263a",
						active: false
					}
				],
				thickness: [ //画笔粗细 
					{
						thickness: 10,
						active: false
					},
					{
						thickness: 20,
						active: false
					},
					{
						thickness: 30,
						active: true
					},
					{
						thickness: 40,
						active: false
					},
					{
						thickness: 50,
						active: false
					},
					{
						thickness: 60,
						active: false
					}
				],
			}
		},
		onLoad() {
			this.getTime(this);

			this.ctx = uni.createCanvasContext("myCanvas", this); //绘画对象
             this.clear();
             			this.showTools();
		},

		methods: {
              	showTools(){
              				this.flag = !this.flag;
              			},
						mPen(){
										this.myPen_flag = !this.myPen_flag;
									},
			// 自定义tabbar页面展示
			switchDialog() {
				let bool = this.show;
				this.setData({
					show: !bool
				});
			},

			mPen() {
				this.myPen_flag = !this.myPen_flag;
			},
			//获取当前时间
			getTime() {
				let date = new Date();

				var y = date.getFullYear();

				var m = date.getMonth();
				switch (m) {
					case 0:
						m = '1';
						break;
					case 1:
						m = '2';
						break;
					case 2:
						m = '3';
						break;
					case 3:
						m = '4';
						break;
					case 4:
						m = '5';
						break;
					case 5:
						m = '6';
						break;
					case 6:
						m = '7';
						break;
					case 7:
						m = '8';
						break;
					case 8:
						m = '9';
						break;
					case 9:
						m = '10';
						break;
					case 10:
						m = '11';
						break;
					case 11:
						m = '12';
						break;
				}

				let d = date.getDate();
				d = d < 10 ? ('0' + d) : d; //天补0

				this.date_d = d;
				this.date_y = y;
				this.date_m = m;
			},

			//绘制功能
			//开始触摸，获取起点
			tstart(e) {
				let startX = e.changedTouches[0].x;
				let startY = e.changedTouches[0].y;
				let sPoint = {
					X: startX,
					Y: startY
				};
				this.point.push(sPoint);
				this.ctx.beginPath();
			},
			//移动手势
			tmove(e) {
				let moveX = e.changedTouches[0].x;
				let moveY = e.changedTouches[0].y;
				let movePoint = {
					X: moveX,
					Y: moveY
				};
				this.point.push(movePoint); //存点
				if (this.point.length >= 2) {
					this.draw(); //绘制路径
				}
			},
			//停止手势
			tend() {
				this.point = [];
			},
			//触摸取消

			//绘制
			draw: function() {
				let point1 = this.point[0]
				let point2 = this.point[1]
				this.point.shift()
				// console.log(JSON.stringify(touchs))
				this.ctx.moveTo(point1.X, point1.Y)
				this.ctx.lineTo(point2.X, point2.Y)
				this.ctx.stroke()
				this.ctx.draw(true);
			},
			//清除
			clear: function() {
				let that = this;
				uni.getSystemInfo({
					success: function(res) {
						let canvasw = res.windowWidth;
						let canvash = res.windowHeight;
						that.ctx.clearRect(0, 0, canvasw, canvash);
						that.ctx.draw(true);
					},
				})
			},
			//选择画笔颜色
			updateColor: function(color) {
				console.log(color);
				this.ctx.strokeStyle = color;
			},
			updateThick: function(thickness) {
				console.log(thickness);
				this.ctx.lineWidth = thickness / 10;
			}

		}
	}
</script>








<style>
	@import url("../../static/style/painting_style.css");

	@font-face {
		font-family: 'iconfont';
		src: url('data:font/ttf;charset=utf-8;base64,') format('truetype');
		font-weight: 500;
		font-style: normal;
	}
</style>
